import { Container, Typography, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';

// ----------------------------------------------------------------------

const FAQS = [
    {
        question: 'How do I purchase data?',
        answer: 'Navigate to Purchase > Data from the sidebar menu, select your network and plan, enter the phone number, and click purchase.'
    },
    {
        question: 'How do I fund my wallet?',
        answer: 'Go to Fund Wallet in the sidebar and choose your preferred funding method: Automated Bank Transfer or Manual Bank Transfer.'
    },
    {
        question: 'How can I view my transaction history?',
        answer: 'Click on Transactions in the sidebar to view all your transactions. You can filter by transaction type using the dropdown menu.'
    },
    {
        question: 'What is the Wallet Summary page?',
        answer: 'The Wallet Summary page shows all your deposits and payments in one place, with summary cards displaying total deposits, total payments, and current balance.'
    },
    {
        question: 'How do I change my PIN?',
        answer: 'Navigate to Change Pin from the sidebar menu, enter your current PIN and new PIN, then confirm the change.'
    },
    {
        question: 'What should I do if a transaction fails?',
        answer: 'Failed transactions are automatically refunded to your wallet. You can view the status in your transaction history. If you need assistance, contact support.'
    },
    {
        question: 'How do I upgrade my KYC?',
        answer: 'Click on KYC Upgrade in the sidebar and follow the instructions to submit your verification documents.'
    }
];

// ----------------------------------------------------------------------

export default function FAQs() {
    const { themeStretch } = useSettings();

    return (
        <Page title="FAQs">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Frequently Asked Questions"
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
                        { name: 'FAQs' },
                    ]}
                />

                <Typography variant="body1" sx={{ mb: 3 }}>
                    Find answers to commonly asked questions below. If you need further assistance, please contact our support team.
                </Typography>

                {FAQS.map((faq, index) => (
                    <Accordion key={index} sx={{ mb: 1 }}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="subtitle1">{faq.question}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <Typography variant="body2" color="text.secondary">
                                {faq.answer}
                            </Typography>
                        </AccordionDetails>
                    </Accordion>
                ))}
            </Container>
        </Page>
    );
}
